#about-main {
  transition: 1s;
  width: 60%;
  margin: 100px auto 200px;
  text-align: "center";
}

.img-wrapper-about {
  transition: 1s;
  text-align: left;
}

.img-round-about {
  transition: 1s;
  width: 90%;
  border: 8px solid #fdd742;
  border-radius: 50px;
}

@media (max-width: 991px) {
  .img-round-about {
    margin: 0 auto 60px !important;
  }

  .img-wrapper-about {
    text-align: center;
  }
}

@media (max-width: 800px) {
  #about-main {
    width: 90%;
    margin: 100px auto 150px;
  }

}

@media (max-width: 515px) {
  .img-round-about {
    width: 100%;
    border-radius: 25px;
  }
}
