.contact-details {
  border-left: 1px solid #000;
  padding-left: 8px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.contact-details h5 {
  font-size: 16px;
}

.contact-details h4 {
  font-size: 17px;
}

.contact-details h5,
.contact-details h4 {
  margin: 0;
}

.contact-icons,
.material-icons {
  transition: 1s !important;
  margin: 0 5px 0 0 !important;
  padding: 0 !important;
  font-size: 25px !important;
  color: #000;
}

.contact-icons:hover,
.material-icons:hover {
  transition: 1s !important;
  color: #f0d259;
}

#tiktok-icon {
  height: 25.813px;
}

@media (max-width: 768px) {
  input[type="text"],
  textarea,
  input[type="email"] {
    width: 100% !important;
    min-width: 0;
  }
}
